.jumbotronBackground {
    background: url('../../Images/andy-wang-75947.jpg') no-repeat center center fixed;
    background-size: cover;
    width: 100vw;
    height: 500px;
    margin-top: 0px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

}
.Typist .Cursor {
  display: inline-block; }
  .Typist .Cursor--blinking {
    opacity: 1;
    -webkit-animation: blink 1s linear infinite;
            animation: blink 1s linear infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.typing {
    font-size: 45px;
    color: black;
   -webkit-text-fill-color: white;
   -webkit-text-stroke-width: .1px;
   -webkit-text-stroke-color: black;
    }


