.projects {
    width: 100vw;
    height: 1000px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 100px;

}

.projects h1 {
    margin-top: 20px;
    font-size: 40px;
    color: rgb(51, 51, 51)
}

.imageContainer {
    position: relative;
    width: 50%;
}
.hoverImage {
  display: block;
  width: 100%;
  height: auto;
}
.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #B2DDF5;
  opacity: .8;
  overflow: hidden;
  width: 100%;
  height: 0;
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
}
.imageContainer:hover .overlay {
    height: 100%;
}
.hoverTextLeft {
    white-space: nowrap; 
    color: darkblue;
    font-size: 20px;
    position: absolute;
    overflow: hidden;
    top: 90%;
    left: 10%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    display: -ms-flexbox;
    display: flex;
}
